const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list:[
				{id:'1',img:'../static/b.jpg',time:'12小时前',name:'admin',num:'3',title:'云南城投股吧说说股票风险如何控制云南城投股吧说说股票风险如何控制云南城投股吧说说股票风险如何控制'},
				{id:'1',img:'../static/b.jpg',time:'11小时前',name:'admin',num:'3',title:'dgfdhdyju'},
				{id:'1',img:'../static/b.jpg',time:'12小时前',name:'admin',num:'2',title:'云南城投股吧说说股票风险如何控制云南城投股吧'}
			],
			productId: '',
			listInfo: [],
			imgUrl:''
		}
	},
	onLoad(e) {
		this.productId = e.id;
		this.imgUrl = helper.imgUrl;
		this.getInfo();
	},
	methods: {
		getInfo(){
			uni.request({
				url: `${helper.requestUrl}/holes/detial`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					hole_id: this.productId
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == 200) {
						this.listInfo = res.data.data
						// console.log(this.listInfo,'++++')
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
